function exportHTML(arr, color) {

  var list = [];
  var class_name = "subject color6";

  for (var i = 0; i < arr.length; i++) {
    var h = (arr[i]['bottom'] - arr[i]['top']).toString() + 'px';
    var t = (arr[i]['top']).toString() + 'px';
    list.push(
      <div class={class_name} style={{ height: h, top: t }}>
        <h3>밥고 시간</h3>
        <p><em>밥</em><span>먹자</span></p>
      </div>
    )
  }

  return list;
}

